.ui.icon.input>input{
    width: 350px
}
html {
    overflow-y:scroll;
}
/* landing page styles */

body {
    background-color: rgb(234, 234, 234);
  }
  
  .masthead {
    background-image: linear-gradient(
      135deg,
      rgb(24, 42, 115) 0%,
      rgb(33, 138, 174) 69%,
      rgb(32, 167, 172) 89%
    ) !important;
  }
  
  .masthead.segment {
    min-height: 700px;
    padding: 1em 0 !important;
  }
  
  .masthead .ui.menu .ui.button,
  .ui.menu a.ui.inverted.button {
    margin-left: 0.5em;
  }
  
  .masthead h1.ui.header {
    margin-top: 3em;
    margin-bottom: 0em;
    font-size: 4em;
    font-weight: normal;
  }
  
  .masthead h2 {
    font-size: 1.7em;
    font-weight: normal;
  }
  
  .ui.vertical.stripe {
    padding: 8em 0em;
  }
  
  .ui.vertical.stripe h3 {
    font-size: 2em;
  }
  
  .ui.vertical.stripe .button + h3,
  .ui.vertical.stripe p + h3 {
    margin-top: 3em;
  }
  
  .ui.vertical.stripe .floated.image {
    clear: both;
  }
  
  .ui.vertical.stripe p {
    font-size: 1.33em;
  }
  
  .ui.vertical.stripe .horizontal.divider {
    margin: 3em 0em;
  }
  
  .quote.stripe.segment {
    padding: 0em;
  }
  
  .quote.stripe.segment .grid .column {
    padding-top: 5em;
    padding-bottom: 5em;
  }
  
  .footer.segment {
    padding: 5em 0em;
  }
  
  .secondary.inverted.pointing.menu {
    border: none;
  }
  
  /* event list css */
  
  .ui.menu .item img.logo {
    margin-right: 1.5em;
  }
  
  .ui.fixed.menu {
    background-image: linear-gradient(
      135deg,
      rgb(24, 42, 115) 0%,
      rgb(33, 138, 174) 69%,
      rgb(32, 167, 172) 89%
    ) !important;
    height: 10%;
  }
  
  .main.container,
  .main.segment {
    margin-top: 2em;
  }
  
  .ui.center.aligned.segment.attendance-preview {
    background-color: #f5f5f5;
  }
  
  /* allow inline equal width labels for semantic  */
  .equal-width label {
    min-width: 100px; /* set this to max width of your label text */
  }
  
  /* sticky footer */
  
  /* .ui.footer.segment {
      position: fixed;
      padding: 10px;
      bottom: 0;
      width: 100%;
      margin-bottom: 0;
      background-color: rgb(234, 234, 234);
    } */
  
  .ui.footer.segment .ui.divider {
    margin-top: 0px;
  }
  
  /* event-detail styles */
  
  /* .event-image {
    background: linear-gradient(rgba(20, 20, 20, 0.2), rgba(20, 20, 20, 0.2));
  } */
  
  .event-image {
      filter: brightness(30%);
    }
  
  .event-image-text {
    position: absolute;
    bottom: 5%;
    left: 5%;
    width: 100%;
    height: auto;
    color: white;
  }
  
  /* remove border from inverted segments */
  .ui.inverted.attached.segment {
      border: none;
  }
  .createJobform {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
  }
  pre{
    font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
    font-size: 1em
  }
  pre{
    margin: 0 0 0 0
  }